<template>
	<view class="kecheng">
		<!-- <view class="header">课程</view> -->
		<view style="width: 100%; height: 52rpx;"></view>
		<view class="header">
		<image class="immg" style="width: 36rpx; height: 36rpx;" src="../../static/img/sousuo.png"></image>
		
			<input type="text" v-model="tittle" placeholder="请输入关键字"  />
			<!-- <view class="iig">
				<image style="width: 1rem; height: 1rem;" src="../../static/img/ss2.png"></image>
			</view> -->
			<view @click="serchh"  style="position: relative; top: 106rpx; left: -90rpx; color: #ff6600;">搜索</view>	
		</view>
<!-- 		<view class="catergory">
			<view class="catergoryL">
				分类
			</view>
			<view class="catergoryR">
				<ul>
					<li @click="btnn(0)" :class="[flagg0===true?'active':'']">全部</li>
					<li  v-for="(item,index) in caterggory":key="index" @click="btn(index)" :class="{active:flag==index+1}">{{item.name}}</li>
				<!-- 	<li @click="btn(1)" :class="[flag1===true?'active':'']">网络项目</li>
					<li @click="btn(2)" :class="[flag2===true?'active':'']">视频教学</li>
					<li @click="btn(3)" :class="[flag3===true?'active':'']">科技软件</li> -->
			<!--	</ul>
				<ul style="margin-top: 10rpx;">
					<li @click="btnn(1)" :class="[flagg1===true?'active':'']">合作专区</li>
					<li @click="btnn(2)" :class="[flagg2===true?'active':'']">成为代理</li>
				</ul>
			</view>
		</view> -->
		<view class="jpkc">
			<!-- <view class="jpkcTitle">发布课程</view> -->
			<view class="jpkcContent">
				<ul>
					<li  @click="kcc(item.title)" v-for="(item,index) in works" :key="index">
						<view class="jpkcLeft">
							<view class="jpkcImg">
								<image style="width:180rpx; height:180rpx" :src="item.logo"></image>
							</view>
							<view class="jpkczi">
								<p style=" font-weight: bold;">{{item.tt}}…</p>
				
								<view class="jpkcBottom">
									<view style="margin-top: 20rpx; color: #7cd5c3; font-size: 24rpx;" v-if="item.qx==0">权限：免费</view>
									<view style="margin-top: 20rpx; color: #cc0033; font-size: 24rpx;" v-if="item.qx==1">权限：VIP会员</view>
									<view style="margin-top: 18rpx; color: #adadad; font-size: 24rpx;" class="jpkcPrice">{{item.up_time}}</view>
								</view>
								
							</view>
						</view>						
					</li>
					
					
				</ul>
			</view>
			
			
		</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
				flagg0:true,
				flagg1:false,
				flagg2:false,
				works:[],
				idds:0,
				caterggory:[],
				flag:"",
				fl:"",
				tittle:[]
			}
		},
		onLoad(options) {
			uni.showShareMenu({
				 withShareTicket:true,
				//设置下方的Menus菜单，才能够让发送给朋友与分享到朋友圈两个按钮可以点击
				menus:["shareAppMessage","shareTimeline"]
			})
			console.log("optt")
			console.log(options)
			this.flag=options.id
			this.fl=options.id
			console.log(this.fl)	
		},
		onShow(){
			uni.switchTab({
				url:"/pages/index/index"
			})
			// this.btn(this.idds)	
			//let that = this
			if(uni.getStorageSync("token")==""){
				console.log("空")
				uni.navigateTo({
					url:"/pages/login/login"
				})
			}else{
				if(this.fl != undefined){
					this.flagg0=false
					uni.request({
						method:'POST',
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/select_works_fl",
						data:JSON.stringify({
							'fl':this.fl
						}),
									
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							for(let i=0; i<this.works.length; i++){
								this.works[i]['tt']=this.works[i].title.substring(0,32)
							}
							console.log(this.works)
						}
					})					
				}
				if(this.fl == undefined){
					uni.request({
						
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/select_works_all",
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							for(let i=0; i<this.works.length; i++){
								this.works[i]['tt']=this.works[i].title.substring(0,32)
							}
							console.log(this.works)
						}
					})				
				}

				
				uni.request({
					
					//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
					url:this.apiUrl+"index.php/index/select_workslb_all",
					success: (res) => {
						console.log("res")
						console.log(res)
						this.caterggory = res.data
						console.log(this.caterggory)
					}
				})				
			}

		},
		methods: {
			btn(index){
				
				this.flag=index+1
				console.log(this.flag)
				if(this.flag > 0){
					this.flagg0=false
					this.flagg1=false
					this.flagg2=false
				}
					uni.request({
						method:'POST',
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/select_works_fl",
						data:JSON.stringify({
							'fl':this.flag
						}),
				
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							for(let i=0; i<this.works.length; i++){
								this.works[i].title=this.works[i].title.substring(0,36)
							}
							console.log(this.works)
						}
					})				

				
			},
			btnn(id){
				if(id==0){
					this.flag="",
					this.flagg0=true
					this.flagg1=false
					this.flagg2=false
					uni.request({
						
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/select_works_all",
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							console.log(this.works)
						}
					})
				}
				if(id==1){
					this.flag="",
					this.flagg0=false
					this.flagg1=true
					this.flagg2=false					
				}
				if(id==2){
					this.flag="",
					this.flagg0=false
					this.flagg1=false
					this.flagg2=true					
				}
			},
	
			kcc(title){
				console.log(title)
				uni.navigateTo({
					url:"/pages/kecheng/kechengDetail?title="+title
				})
			},
			serchh(){
				console.log("搜")
				console.log(this.tittle)
				uni.request({
					method:"POST",
					url:this.apiUrl+"index.php/index/ss_works",
					data:JSON.stringify({
						"title" : this.tittle
					}),
					success: (res) => {
						console.log("搜索")
						console.log(res)
						this.works = res.data
					}
				})
			}
		}
	}
</script>

<style>
.kecheng .header{
	width:100%;
	height: 180rpx;
	display: flex;
	border-bottom: 4rpx solid #f2f2f2;
	border-top: 4rpx solid #f2f2f2;
	
	margin-left: 28rpx;
}
 	.kecheng .header .iig{
		margin-top: 104rpx;
		position: relative;
		top: 10rpx;
		left: -120rpx;
	}
	.kecheng .header input{
		border:none;
		width: 50%;
		
		border-radius: 80rpx;
		background-color: #f2f2f2;
		 margin-top: 104rpx;	
		padding-left: 50rpx;
		color: #bababa;
		box-shadow: 0 0 3px 2px #ededed;
	}
	.kecheng .immg{
		position: relative;
		top: 112rpx;
		left: 50rpx;
	}
.kecheng .catergory{
	display: flex;
	margin-top: 20rpx;
}
.kecheng .catergory .catergoryL{
	font-weight: bold;
	margin-left: 28rpx;
	
	width: 80rpx;
}
.kecheng .catergory .catergoryR{
	font-size: 30rpx;
}
.kecheng .catergory .catergoryR ul{
	display: flex;
}
.kecheng .catergory .catergoryR ul li{
	padding: 6rpx 14rpx;
	margin-left: 32rpx;
}
.active{
	background-color: #ffe3c6;
	color: #ff6600;
	padding: 6rpx 14rpx;
	border-radius: 30rpx;
}
/* 	.kecheng .jpkc{
		margin-top: 78rpx;
	} */
	.kecheng .jpkc .jpkcTitle{
		width: 95%;
		font-weight: bold;
		margin: 0 auto;
	}
	.kecheng .jpkc .jpkcContent{
		width: 96%;
		margin: 0 auto;
	}
	.kecheng .jpkc .jpkcContent ul li{
		display: flex;
		background-color: #fff;
		justify-content: space-between;
		margin-top: 10rpx;
		padding: 20rpx;
		box-shadow: 0 0 4px 4px rgba(240,240,240,0.5);
		border-radius: 10rpx;
	}
	.kecheng .jpkc .jpkcContent ul li:first-child{
		margin-top: 30rpx;
	}
	.kecheng .jpkc .jpkcContent ul li .jpkcLeft{
		display: flex;
	}
	.kecheng .jpkc .jpkcContent ul li .jpkcLeft .jpkczi{
		margin-left: 14rpx;
		width: 440rpx;
		
		font-size: 28rpx;
	}


	.kecheng .jpkc .jpkcContent ul li .jpkcLeft .jpkcImg{
		border: 2rpx solid #f8f8f8;
	}
</style>
